<template>
    <div class="card mt-2" v-for="(todo, index) in todos" :key="todo.id">
      <div class="card-body p-2">
        <div class="form-check">
          <input class="form-check-input" type="checkbox" v-model="todo.fin" :checked="todo.fin" @change="toggleTodo(index)"/>
          <label class="form-check-lable" :class="{ todoFin: todo.fin }">{{todo.subject}}</label>
        </div>
        <div>
          <button class="btn btn-danger" @click="todoDelete(index)">삭제</button>
        </div>
      </div>
    </div>
</template>

<script>
export default {
  props: ['todos'],
  emits: ['toogle-todo', 'delete-todo'],
  setup(props, {emit}) {
    const toggleTodo = (index) => {
      emit('toogle-todo', index);
    }
    const todoDelete = (index) => {
      emit('delete-todo', index);
    }
    return {
      toggleTodo,
      todoDelete
    }
  },
}
</script>

