<template>
  <form class="form" @submit.prevent="onSubmit">
    <input type="text" class="name form-control" v-model="todo" placeholder="입력"/>
    <button class="btn btn-primary" type="submit">추가</button>
  </form>
  <span v-if="errorStatus" style="display: block; margin: 10px 0; color: red; font-weight: bold">공백은 사용 할 수 없습니다.</span>
</template>

<script>
import { ref } from 'vue';

export default {
  emits: ['todo-add'],
  setup(props, {emit}) {
    const todo = ref('');
    const errorStatus = ref(false);

    const onSubmit = () => {
      if (todo.value == '') {
        errorStatus.value = true;
      } else {
        emit('todo-add', {
          id: Date.now(),
          subject: todo.value,
          fin: false,
        })
        todo.value = '';
        errorStatus.value = false;
      }
    }
    return {
      onSubmit,
      todo,
      errorStatus,
    }
  }
}
</script>

