<template>
  <link href="https://cdn.jsdelivr.net/npm/bootstrap@5.3.0-alpha3/dist/css/bootstrap.min.css" rel="stylesheet" integrity="sha384-KK94CHFLLe+nY2dmCWGMq91rCGa5gtU4mk92HdvYe+M/SXH301p5ILy+dN9+nJOZ" crossorigin="anonymous"/>
  <div style="background-color: #eee; padding: 20px;">
    <div class="container">
      <h2>TODO LIST</h2>
      <input type="text" class="name form-control mb-3" style="border-radius: 0.375rem!important" v-model="searchText" placeholder="검색"/>
      <hr>
      <todoSimpleForm @todo-add="addTodo" />
      <span style="color:red">{{ errorMsg }}</span>
      <span v-if="!searchTextFiltered.length" style="display: block; margin: 10px 0; font-weight: bold">결과 없음</span>
      <todoList :todos="searchTextFiltered" @toogle-todo="toggleTodo" @delete-todo="todoDelete" />
    </div>
  </div>
</template>

<script>
import { ref, computed } from 'vue';
import todoSimpleForm from './components/todoSimpleForm.vue';
import todoList from './components/todoList.vue';
import { initializeApp } from "firebase/app";
import { getFirestore, collection, addDoc, doc, getDoc, getDocs, deleteDoc, updateDoc, serverTimestamp, query, orderBy} from "firebase/firestore";

const firebaseConfig = {
  apiKey: "AIzaSyByI-U0rBpYIMCHCKb2ECBMtJM1L7aiPrc",
  authDomain: "todo-23de4.firebaseapp.com",
  projectId: "todo-23de4",
  storageBucket: "todo-23de4.appspot.com",
  messagingSenderId: "982312194094",
  appId: "1:982312194094:web:658dc0c7ace3e6e1328fbf"
};

const app = initializeApp(firebaseConfig);
const db = getFirestore(app);

export default {
  components: {
    todoSimpleForm,
    todoList,
  },

  setup() {
    const todos = ref([]);
    const errorMsg = ref('');

    // 데이터 불러오기
    const getTodo = async () => {
      try {
        const getAllData = await getDocs(query(collection(db, 'todos'), orderBy('timestamp')));
        todos.value = getAllData.docs.map(doc => doc.data());
      } catch (err) {
        errorMsg.value = 'DB error';
      }
    }

    getTodo();
    
    // 데이터 추가하기
    const addTodo = async (todo) => {
      try {
        const res = await addDoc(collection(db, "todos"), {
        timestamp: serverTimestamp(),
        subject: todo.subject,
        fin:todo.fin,
      });
      const getData = await getDoc(doc(db, 'todos', res.id));
      todos.value.push(getData.data());
      } catch(err) {
        errorMsg.value = 'DB error';
      }
    }

    // 데이터 삭제
    const todoDelete = async (index) => {
      try {
        const getDocumentId = await getDocs(query(collection(db, 'todos'), orderBy('timestamp')));
        const getThisId = (await getDocumentId).docs.map(doc => doc.id);
        await deleteDoc(doc(db, "todos", getThisId[index]));
        todos.value.splice(index, 1);
      } catch {
        errorMsg.value = 'DB error';
      }
    }

    // 체크박스 토글
    const toggleTodo = async (index) => {
      try {
        const getDocumentId = await getDocs(query(collection(db, 'todos'), orderBy('timestamp')));
        const getThisId = (await getDocumentId).docs.map(doc => doc.id);
        const thisDocument = await doc(db, 'todos', getThisId[index]);
        await updateDoc(thisDocument, {
          fin: todos.value[index].fin
        });  
      } catch {
        errorMsg.value = 'DB error';
      }
    }

    const searchText = ref('');
    const searchTextFiltered = computed(() => {
      if (searchText.value) {
        return todos.value.filter(todo => {
          return todo.subject.includes(searchText.value);
        });
      }
      return todos.value;
    });

    return {
      todos,
      todoDelete,
      addTodo,
      toggleTodo,
      searchText,
      searchTextFiltered,
      errorMsg,
      db,
    }
  },
}
</script>

